import axios from 'axios'
export function request(config,baseurl) {
  // 1.axios
  const instance = axios.create({
      baseURL:baseurl,
    timeout: 5000,
  })
    // 2.1.
  instance.interceptors.request.use(config => {
    return config
  }, err => {
    console.log(err);
  })
  // 2.2.
  instance.interceptors.response.use(res => {
    return res.data
  }, err => {
    console.log(err);
  })
  // 3.
  return instance(config)
}